import { Routes, Route, Navigate } from 'react-router-dom';
import useAuth from 'hooks/useAuth';

import IndexLayout from 'layouts/IndexLayout';
import MainLayout from 'layouts/MainLayout';
import AuthRoutes from 'components/AuthRoutes';
import IndexPage from 'pages/index/IndexPage';
import Page from 'components/Page';
import AccountPages from 'pages/account';
import HomePage from './pages/home/HomePage';
import IndexPages from './pages/index';
import ErrorPages from './pages/errors';
import 'primereact/resources/primereact.css';
import 'primeicons/primeicons.css';
import 'primeflex/primeflex.css';
import 'assets/styles/layout.scss';
const App = () => {
	const auth = useAuth();
	function DefaultPage(){
		if(!auth.isLoggedIn){
			return <IndexPage />
		}
		return <Navigate to="/home" replace />;
	}
	return (
		<Routes>
			<Route exact element={<AuthRoutes />}>
			<Route element={<MainLayout />}>
				<Route path="/home" element={<HomePage />} />
				

				{/* kbm_guru pages routes */}
				<Route path="/kbm_guru" element={<Page path="kbm_guru/List"/>} />
				<Route path="/kbm_guru/:fieldName/:fieldValue" element={<Page path="kbm_guru/List"/>} />
				<Route path="/kbm_guru/index/:fieldName/:fieldValue" element={<Page path="kbm_guru/List"/>} />
				<Route path="/kbm_guru/view/:pageid" element={<Page path="kbm_guru/View"/>} />
				<Route path="/kbm_guru/add" element={<Page path="kbm_guru/Add"/>} />
				<Route path="/kbm_guru/edit/:pageid" element={<Page path="kbm_guru/Edit"/>} />

				{/* laporan pages routes */}
				<Route path="/laporan" element={<Page path="laporan/List"/>} />
				<Route path="/laporan/:fieldName/:fieldValue" element={<Page path="laporan/List"/>} />
				<Route path="/laporan/index/:fieldName/:fieldValue" element={<Page path="laporan/List"/>} />

				{/* permissions pages routes */}
				<Route path="/permissions" element={<Page path="permissions/List"/>} />
				<Route path="/permissions/:fieldName/:fieldValue" element={<Page path="permissions/List"/>} />
				<Route path="/permissions/index/:fieldName/:fieldValue" element={<Page path="permissions/List"/>} />
				<Route path="/permissions/view/:pageid" element={<Page path="permissions/View"/>} />
				<Route path="/permissions/add" element={<Page path="permissions/Add"/>} />
				<Route path="/permissions/edit/:pageid" element={<Page path="permissions/Edit"/>} />

				{/* roles pages routes */}
				<Route path="/roles" element={<Page path="roles/List"/>} />
				<Route path="/roles/:fieldName/:fieldValue" element={<Page path="roles/List"/>} />
				<Route path="/roles/index/:fieldName/:fieldValue" element={<Page path="roles/List"/>} />
				<Route path="/roles/view/:pageid" element={<Page path="roles/View"/>} />
				<Route path="/roles/add" element={<Page path="roles/Add"/>} />
				<Route path="/roles/edit/:pageid" element={<Page path="roles/Edit"/>} />

				{/* tbl_bidang_study pages routes */}
				<Route path="/tbl_bidang_study" element={<Page path="tbl_bidang_study/List"/>} />
				<Route path="/tbl_bidang_study/:fieldName/:fieldValue" element={<Page path="tbl_bidang_study/List"/>} />
				<Route path="/tbl_bidang_study/index/:fieldName/:fieldValue" element={<Page path="tbl_bidang_study/List"/>} />
				<Route path="/tbl_bidang_study/view/:pageid" element={<Page path="tbl_bidang_study/View"/>} />
				<Route path="/tbl_bidang_study/add" element={<Page path="tbl_bidang_study/Add"/>} />
				<Route path="/tbl_bidang_study/edit/:pageid" element={<Page path="tbl_bidang_study/Edit"/>} />

				{/* tbl_guru pages routes */}
				<Route path="/tbl_guru" element={<Page path="tbl_guru/List"/>} />
				<Route path="/tbl_guru/:fieldName/:fieldValue" element={<Page path="tbl_guru/List"/>} />
				<Route path="/tbl_guru/index/:fieldName/:fieldValue" element={<Page path="tbl_guru/List"/>} />
				<Route path="/tbl_guru/view/:pageid" element={<Page path="tbl_guru/View"/>} />
				<Route path="/tbl_guru/add" element={<Page path="tbl_guru/Add"/>} />
				<Route path="/tbl_guru/edit/:pageid" element={<Page path="tbl_guru/Edit"/>} />

				{/* tbl_jam pages routes */}
				<Route path="/tbl_jam" element={<Page path="tbl_jam/List"/>} />
				<Route path="/tbl_jam/:fieldName/:fieldValue" element={<Page path="tbl_jam/List"/>} />
				<Route path="/tbl_jam/index/:fieldName/:fieldValue" element={<Page path="tbl_jam/List"/>} />
				<Route path="/tbl_jam/view/:pageid" element={<Page path="tbl_jam/View"/>} />
				<Route path="/tbl_jam/add" element={<Page path="tbl_jam/Add"/>} />
				<Route path="/tbl_jam/edit/:pageid" element={<Page path="tbl_jam/Edit"/>} />

				{/* tbl_kelas pages routes */}
				<Route path="/tbl_kelas" element={<Page path="tbl_kelas/List"/>} />
				<Route path="/tbl_kelas/:fieldName/:fieldValue" element={<Page path="tbl_kelas/List"/>} />
				<Route path="/tbl_kelas/index/:fieldName/:fieldValue" element={<Page path="tbl_kelas/List"/>} />
				<Route path="/tbl_kelas/view/:pageid" element={<Page path="tbl_kelas/View"/>} />
				<Route path="/tbl_kelas/add" element={<Page path="tbl_kelas/Add"/>} />
				<Route path="/tbl_kelas/edit/:pageid" element={<Page path="tbl_kelas/Edit"/>} />

				{/* user pages routes */}
				<Route path="/user" element={<Page path="user/List"/>} />
				<Route path="/user/:fieldName/:fieldValue" element={<Page path="user/List"/>} />
				<Route path="/user/index/:fieldName/:fieldValue" element={<Page path="user/List"/>} />
				<Route path="/user/view/:pageid" element={<Page path="user/View"/>} />
				<Route path="/user/add" element={<Page path="user/Add"/>} />
				<Route path="/user/edit/:pageid" element={<Page path="user/Edit"/>} />
				<Route path="/account/*" element={<AccountPages />} />
			</Route>
			</Route>
			<Route exact element={<IndexLayout />}>
				<Route path="/" element={<DefaultPage />} />
				<Route path="/*" element={<IndexPages />} />
				<Route path="/error/*" element={<ErrorPages />} />
			</Route>
		</Routes>
	);
}
export default App;
