import { RecordCount } from 'components/RecordCount';
import { Title } from 'components/Title';

export default function HomePage() {
	
	return (
		<main id="HomePage" className="main-page">
<section className="page-section q-pa-md" >
    <div className="container-fluid">
        <div className="grid ">
            <div className="col comp-grid" >
                <Title title="Home"   titleClass="text-2xl font-bold" subTitleClass="text-sm text-500"       />
            </div>
        </div>
    </div>
</section>
<section className="page-section mb-3" >
    <div className="container-fluid">
        <div className="grid ">
            <div className="col comp-grid" >
                <RecordCount title="Data Guru" description="Total Guru" icon="pi pi-user" apiPath="components_data/getcount_dataguru" link={`/#`}     />
            </div>
        </div>
    </div>
</section>
<section className="page-section mb-3" >
    <div className="container-fluid">
        <div className="grid ">
            <div className="col comp-grid" >
                <RecordCount title="Data Mapel" description="Total Mapel" icon="pi pi-bookmark" apiPath="components_data/getcount_datamapel" link={`/#`}     />
            </div>
        </div>
    </div>
</section>
<section className="page-section mb-3" >
    <div className="container-fluid">
        <div className="grid ">
            <div className="col comp-grid" >
                <RecordCount title="Data Kelas" description="Total Kelas" icon="pi pi-building" apiPath="components_data/getcount_datakelas" link={`/#`}     />
            </div>
        </div>
    </div>
</section>
		</main>
	);
}
